.project{
    text-align: left;
    height: 100%;
    background-color: #DCDCDC;
    margin: 0;
    padding: 60px;
}

.project-box {
    height: 50vh;
    background-color: white;
    transition: 0.2s;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    overflow: hidden;
    
}

.project-img {
    width: 100%;
    height: 60%;
    object-fit: cover;
}

.project-box:hover {
    height: 70vh;
}